export default function ProductPage({ params }: { params: Promise<{ id: string }> }) {
    const resolvedParams = use(params);
    const product = MOCK_PRODUCTS[resolvedParams.id] || MOCK_PRODUCTS['1'];
    const { addItem } = useCart();
    const [isAdding, setIsAdding] = useState(false);
    const [currentImage, setCurrentImage] = useState(0);
    const [isWishlisted, setIsWishlisted] = useState(false);

    const handleAddToCart = () => {
        setIsAdding(true);
        addItem({
            id: product.id,
            name: product.name,
            price: product.price,
            image: product.images[0]
        });
        setTimeout(() => setIsAdding(false), 800);
    };

    const nextImage = () => {
        setCurrentImage((prev) => (prev + 1) % product.images.length);
    };

    const prevImage = () => {
        setCurrentImage((prev) => (prev - 1 + product.images.length) % product.images.length);
    };

    return (
        <div style={{ maxWidth: '1400px', margin: '0 auto', padding: '0 24px' }}>
            <section style={{ paddingTop: '140px', paddingBottom: '80px' }}>
                {/* Breadcrumb */}
                <div style={{ marginBottom: '32px' }}>
                    <Link href="/products" style={{
                        color: '#888',
                        textDecoration: 'none',
                        fontSize: '0.9rem',
                        display: 'flex',
                        alignItems: 'center',
                        gap: '8px'
                    }}>
                        <ChevronLeft size={16} /> Back to Products
                    </Link>
                </div>

                <div style={{
                    display: 'grid',
                    gridTemplateColumns: 'repeat(auto-fit, minmax(400px, 1fr))',
                    gap: '60px',
                    alignItems: 'start'
                }}>
                    {/* Image Gallery */}
                    <div>
                        <div style={{
                            position: 'relative',
                            aspectRatio: '1',
                            borderRadius: '24px',
                            overflow: 'hidden',
                            backgroundColor: '#f5f5f7',
                            marginBottom: '16px'
                        }}>
                            <img
                                src={product.images[currentImage]}
                                alt={product.name}
                                style={{
                                    width: '100%',
                                    height: '100%',
                                    objectFit: 'cover'
                                }}
                            />

                            {/* Image Navigation */}
                            {product.images.length > 1 && (
                                <>
                                    <button
                                        onClick={prevImage}
                                        style={{
                                            position: 'absolute',
                                            left: '16px',
                                            top: '50%',
                                            transform: 'translateY(-50%)',
                                            width: '44px',
                                            height: '44px',
                                            borderRadius: '50%',
                                            backgroundColor: 'rgba(255,255,255,0.9)',
                                            border: 'none',
                                            cursor: 'pointer',
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                            boxShadow: '0 4px 12px rgba(0,0,0,0.15)'
                                        }}
                                    >
                                        <ChevronLeft size={20} />
                                    </button>
                                    <button
                                        onClick={nextImage}
                                        style={{
                                            position: 'absolute',
                                            right: '16px',
                                            top: '50%',
                                            transform: 'translateY(-50%)',
                                            width: '44px',
                                            height: '44px',
                                            borderRadius: '50%',
                                            backgroundColor: 'rgba(255,255,255,0.9)',
                                            border: 'none',
                                            cursor: 'pointer',
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                            boxShadow: '0 4px 12px rgba(0,0,0,0.15)'
                                        }}
                                    >
                                        <ChevronRight size={20} />
                                    </button>
                                </>
                            )}
                        </div>

                        {/* Thumbnails */}
                        {product.images.length > 1 && (
                            <div style={{
                                display: 'flex',
                                gap: '12px'
                            }}>
                                {product.images.map((img, i) => (
                                    <button
                                        key={i}
                                        onClick={() => setCurrentImage(i)}
                                        style={{
                                            width: '80px',
                                            height: '80px',
                                            borderRadius: '12px',
                                            overflow: 'hidden',
                                            border: currentImage === i ? '2px solid #000' : '2px solid transparent',
                                            padding: 0,
                                            cursor: 'pointer',
                                            backgroundColor: '#f5f5f7'
                                        }}
                                    >
                                        <img
                                            src={img}
                                            alt=""
                                            style={{ width: '100%', height: '100%', objectFit: 'cover' }}
                                        />
                                    </button>
                                ))}
                            </div>
                        )}
                    </div>

                    {/* Product Details */}
                    <div style={{ padding: '20px 0' }}>
                        <div style={{
                            fontSize: '0.75rem',
                            color: '#888',
                            fontWeight: 600,
                            textTransform: 'uppercase',
                            letterSpacing: '0.1em',
                            marginBottom: '12px'
                        }}>
                            {product.category}
                        </div>

                        <h1 style={{
                            fontSize: 'clamp(2rem, 4vw, 2.5rem)',
                            fontWeight: 700,
                            marginBottom: '16px',
                            lineHeight: 1.2,
                            letterSpacing: '-0.02em'
                        }}>
                            {product.name}
                        </h1>

                        <p style={{
                            fontSize: '2rem',
                            fontWeight: 700,
                            marginBottom: '24px'
                        }}>
                            ₹{product.price.toLocaleString('en-IN')}
                        </p>

                        <p style={{
                            color: '#555',
                            lineHeight: 1.7,
                            marginBottom: '32px',
                            fontSize: '1rem'
                        }}>
                            {product.description}
                        </p>

                        {/* Action Buttons */}
                        <div style={{ display: 'flex', gap: '16px', marginBottom: '40px' }}>
                            <button
                                onClick={handleAddToCart}
                                disabled={isAdding}
                                style={{
                                    flex: 1,
                                    padding: '18px 32px',
                                    backgroundColor: isAdding ? '#333' : '#000',
                                    color: '#fff',
                                    borderRadius: '50px',
                                    fontWeight: 600,
                                    fontSize: '1rem',
                                    border: 'none',
                                    cursor: isAdding ? 'default' : 'pointer',
                                    transition: 'all 0.2s ease'
                                }}
                            >
                                {isAdding ? '✓ Added to Cart' : 'Add to Cart'}
                            </button>
                            <button
                                onClick={() => setIsWishlisted(!isWishlisted)}
                                style={{
                                    width: '60px',
                                    height: '60px',
                                    borderRadius: '50%',
                                    border: '1px solid #e5e5e5',
                                    backgroundColor: isWishlisted ? '#ffe0e0' : '#fff',
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    cursor: 'pointer',
                                    transition: 'all 0.2s ease'
                                }}
                            >
                                <Heart size={22} fill={isWishlisted ? '#ff4444' : 'none'} color={isWishlisted ? '#ff4444' : '#000'} />
                            </button>
                        </div>

                        {/* Features */}
                        <div style={{
                            borderTop: '1px solid #f0f0f0',
                            paddingTop: '32px',
                            marginBottom: '32px'
                        }}>
                            <h3 style={{ fontSize: '1.1rem', fontWeight: 600, marginBottom: '20px' }}>
                                Key Features
                            </h3>
                            <ul style={{ listStyle: 'none', padding: 0, margin: 0 }}>
                                {product.features.map((feature, i) => (
                                    <li key={i} style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                        gap: '12px',
                                        marginBottom: '14px',
                                        color: '#444',
                                        fontSize: '0.95rem'
                                    }}>
                                        <span style={{
                                            width: '8px',
                                            height: '8px',
                                            backgroundColor: '#000',
                                            borderRadius: '50%',
                                            flexShrink: 0
                                        }}></span>
                                        {feature}
                                    </li>
                                ))}
                            </ul>
                        </div>

                        {/* Trust Badges */}
                        <div style={{
                            display: 'grid',
                            gridTemplateColumns: 'repeat(3, 1fr)',
                            gap: '16px',
                            padding: '24px',
                            backgroundColor: '#f9f9f9',
                            borderRadius: '16px'
                        }}>
                            <div style={{ textAlign: 'center' }}>
                                <Truck size={24} style={{ marginBottom: '8px' }} />
                                <div style={{ fontSize: '0.75rem', fontWeight: 600 }}>Free Shipping</div>
                            </div>
                            <div style={{ textAlign: 'center' }}>
                                <Shield size={24} style={{ marginBottom: '8px' }} />
                                <div style={{ fontSize: '0.75rem', fontWeight: 600 }}>1 Year Warranty</div>
                            </div>
                            <div style={{ textAlign: 'center' }}>
                                <RotateCcw size={24} style={{ marginBottom: '8px' }} />
                                <div style={{ fontSize: '0.75rem', fontWeight: 600 }}>Easy Returns</div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}
